.development__info { margin-top: 40px; font-size: 16px; color: #ff151f; }
.development__link { display: block; color: #ff151f; }
.development__link:hover { text-decoration: underline; }
.development h4 { font-size: 36px; }
.development h5 { font-size: 24px; }
@media only screen and (max-width: 1024px) { .development__bl { font-size: 18px; }
  .development__num { font-size: 25px; }
  .development h4 { font-size: 25px; }
  .development h5 { font-size: 20px; } }
@media only screen and (max-width: 900px) { .development__in { display: block; }
  .development__bl { display: block; text-align: center; }
  .development__bl.txt-center { width: 100%; border: none; }
  .development__bl.txt-right { text-align: center; }
  .development__txt { max-width: initial; margin: 0 auto 30px; } }
@media only screen and (max-width: 650px) { .development__ttl h2:before, .development__ttl h2:after { display: none; }
  .development__ttl span:before, .development__ttl span:after { display: none; } }

.contacts { padding: 60px 0 90px; background: url(../img/advantage-bg.jpg) repeat; }
.contacts__in { display: table; width: 100%; table-layout: fixed; }
.contacts__bl { display: table-cell; padding: 0 20px; font-size: 16px; color: #fff; vertical-align: top; }
.contacts__bl.is-form { width: 340px; }
.contacts__txt { margin-bottom: 35px; }
.contacts__subttl { color: #ffebb8; }
.contacts__tel { margin-top: 37px; text-transform: uppercase; font-size: 24px; font-family: "opensans-semibold", Arial, sans-serif; }
.contacts h3 { margin-bottom: 33px; color: #ffebb8; }
.contacts span { display: inline-block; white-space: nowrap; }
@media only screen and (max-width: 900px) { .contacts__in { display: block; }
  .contacts__bl { display: block; }
  .contacts__bl:first-child { text-align: center; }
  .contacts__bl:first-child .contacts__txt { max-width: 500px; margin: 0 auto 35px; }
  .contacts__bl.is-form { width: 100%; padding: 0; text-align: center; } }

.form { position: relative; max-width: 350px; text-align: left; padding: 35px 30px 25px; background-color: #ffebb8; }
.form.no-bg { background: none; }
.form__ttl { margin-bottom: 25px; text-transform: uppercase; font-size: 30px; color: #111212; line-height: 30px; font-family: "opensans-semibold", Arial, sans-serif; }
.form__label { display: block; margin-bottom: 12px; }
.form__btn { display: block; width: 100%; margin-top: 15px; padding: 9px; }
@media only screen and (max-width: 900px) { .form { margin: 0 auto; } }

.doc { background: #213943; }
.doc__in { position: relative; padding: 50px 0; top: -40px; text-align: center; font-size: 0; background: #335361; }
.doc__ttl { margin-bottom: 30px; text-transform: uppercase; text-align: center; font-size: 30px; color: #ffebb8; line-height: 1.24; }
.doc__wrap { display: inline-block; width: 33.33%; padding: 0 20px; text-align: left; vertical-align: top; }
.doc__bl { display: block; margin-bottom: 30px; font-size: 0; color: #FFF; *zoom: 1; }
.doc__bl:after { content: " "; display: table; clear: both; }
.doc__bl:hover span { text-decoration: none; }
.doc__bl span { display: inline-block; width: 80%; padding-left: 15px; font-size: 16px; vertical-align: middle; text-decoration: underline; }
.doc__ico { display: inline-block; width: 20%; max-width: 43px; vertical-align: middle; }
.doc .icon { vertical-align: middle; }
@media only screen and (max-width: 900px) { .doc__wrap { width: 50%; } }
@media only screen and (max-width: 650px) { .doc__wrap { width: 100%; }
  .doc__bl { max-width: 350px; margin: 0 auto 30px; } }

#map { height: 400px; }
@media only screen and (max-width: 900px) { #map { height: 300px; } }
@media only screen and (max-width: 650px) { #map { height: 200px; } }

.help-block.form-error { padding-top: 5px; font-size: 12px; color: #e1000a; }

.slider { width: 100%; height: 100%; }
.slider__item { width: 100%; height: 100%; background-repeat: no-repeat; background-size: cover; background-position: 50% 50%; }
.slider .slick-list { width: 100%; }
.slider .slick-track, .slider .slick-list { height: 100%; }
.slider .slick-prev, .slider .slick-next { position: absolute; top: auto; bottom: 50px; margin: 0; width: 65px; height: 65px; background-color: rgba(19, 29, 45, 0.6); border: 2px solid #FFF; border-radius: 50%; z-index: 100; transition: 0.3s; }
.slider .slick-prev:hover, .slider .slick-next:hover { opacity: 0.7; }
.slider .slick-prev:before, .slider .slick-next:before { content: ""; position: absolute; top: 50%; left: 50%; }
.slider .slick-prev { left: 40px; }
.slider .slick-prev:before { width: 0px; height: 0px; border-style: solid; border-width: 12px 18px 12px 0; border-color: transparent #fff transparent transparent; margin: -12px 0 0 -12px; }
.slider .slick-next { right: 40px; }
.slider .slick-next:before { width: 0px; height: 0px; border-style: solid; border-width: 12px 0 12px 18px; border-color: transparent transparent transparent #fff; margin: -12px 0 0 -9px; }
@media only screen and (max-width: 650px) { .slider .slick-prev, .slider .slick-next { width: 40px; height: 40px; }
  .slider .slick-prev { left: 20px; }
  .slider .slick-prev:before { width: 0px; height: 0px; border-style: solid; border-width: 10px 15px 10px 0; border-color: transparent #fff transparent transparent; margin: -10px 0 0 -10.5px; }
  .slider .slick-next { right: 20px; }
  .slider .slick-next:before { width: 0px; height: 0px; border-style: solid; border-width: 10px 0 10px 15px; border-color: transparent transparent transparent #fff; margin: -10px 0 0 -7.5px; } }

.popup { display: none; position: fixed; top: 0; left: 0; width: 100%; min-width: 320px; height: 100%; background: rgba(0, 0, 0, 0.9); padding: 20px; overflow: auto; z-index: 999; white-space: nowrap; text-align: center; font-size: 0; }
.popup:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.popup.is-gallery { padding: 0; }
.popup.is-gallery:before { display: none; }
.popup .popup__in { position: relative; display: inline-block; padding: 114px 65px; font-size: 16px; background: #FFF; border-radius: 20px; white-space: normal; vertical-align: middle; }
.popup .popup__close { position: fixed; top: 30px; right: 40px; width: 65px; height: 65px; background-color: rgba(19, 29, 45, 0.6); border: 2px solid #FFF; border-radius: 50%; z-index: 100; transition: 0.3s; text-align: center; font-size: 0; }
.popup .popup__close:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.popup .popup__close:hover { opacity: 0.7; }
.popup .popup__close span { display: inline-block; font-size: 40px; line-height: 1; color: #fff; vertical-align: -10px; }
.popup .popup__thanks { max-width: 500px; font-size: 30px; color: #375272; line-height: 1.2; font-family: "opensans-semibold", Arial, sans-serif; }
@media only screen and (max-width: 650px) { .popup { padding: 10px; }
  .popup .popup__close { right: 20px; width: 40px; height: 40px; }
  .popup .popup__close span { font-size: 20px; vertical-align: -5px; } }

.slick-slider { position: relative; display: block; box-sizing: border-box; -moz-box-sizing: border-box; -webkit-touch-callout: none; -webkit-user-select: none; -khtml-user-select: none; -moz-user-select: none; -ms-user-select: none; user-select: none; -ms-touch-action: pan-y; touch-action: pan-y; -webkit-tap-highlight-color: transparent; }

.slick-list { position: relative; overflow: hidden; display: block; margin: 0; padding: 0; }
.slick-list:focus { outline: none; }
.slick-loading .slick-list { background: white slick-image-url("ajax-loader.gif") center center no-repeat; }
.slick-list.dragging { cursor: pointer; cursor: hand; }

.slick-slider .slick-list, .slick-track, .slick-slide, .slick-slide img { -webkit-transform: translate3d(0, 0, 0); -moz-transform: translate3d(0, 0, 0); -ms-transform: translate3d(0, 0, 0); -o-transform: translate3d(0, 0, 0); transform: translate3d(0, 0, 0); }

.slick-track { position: relative; left: 0; top: 0; display: block; zoom: 1; }
.slick-track:before, .slick-track:after { content: ""; display: table; }
.slick-track:after { clear: both; }
.slick-loading .slick-track { visibility: hidden; }

.slick-slide { float: left; height: 100%; min-height: 1px; display: none; }
.slick-slide img { display: block; max-width: 100%; }
.slick-slide.slick-loading img { display: none; }
.slick-slide.dragging img { pointer-events: none; }
.slick-initialized .slick-slide { display: block; }
.slick-loading .slick-slide { visibility: hidden; }
.slick-vertical .slick-slide { display: block; height: auto; border: 1px solid transparent; }

.slick-prev, .slick-next { position: absolute; top: 50%; margin-top: -14px; font: 0/0 a; text-shadow: none; color: transparent; height: 50px; background: none; }
.slick-prev:focus, .slick-next:focus { outline: none; }
.slick-prev.slick-disabled, .slick-next.slick-disabled { opacity: 0.2; }
.slick-prev:before, .slick-next:before { height: 50px; line-height: 50px; }

.slick-prev { left: 0; }
.slick-prev:before { content: "тЖР"; display: inline-block; vertical-align: middle; font-size: 61px; color: #fff; }

.slick-next { right: 0; }
.slick-next:before { content: "тЖТ"; display: inline-block; vertical-align: middle; font-size: 61px; color: #fff; }

.slick-dots { text-align: center; position: absolute; margin: 0; bottom: 0; left: 0; right: 0; }
.slick-dots li { position: relative; display: inline-block; padding: 0 3px; }
.slick-dots li.slick-active button { opacity: 0.3; box-shadow: none; }
.slick-dots li:before { display: none; }
.slick-dots button { display: block; width: 6px; height: 6px; background: #fff; box-shadow: 0 1px 5px rgba(0, 0, 0, 0.25); cursor: pointer; font: 0/0 a; text-shadow: none; color: transparent; border-radius: 50%; }
.slick-dots button:hover { box-shadow: none; }


